@charset "UTF-8";
@font-face {
  font-family: "Roboto";
  src: local("Roboto"), url("/fonts/roboto.woff2") format("woff2"), url("/fonts/roboto.woff") format("woff");
  font-style: normal;
  font-weight: 400; }

@font-face {
  font-family: "Roboto";
  src: local("Roboto"), url("/fonts/roboto.woff2") format("woff2"), url("/fonts/roboto.woff") format("woff");
  font-style: normal;
  font-weight: 900; }

@font-face {
  font-family: "Roboto";
  src: local("Roboto"), url("/fonts/roboto.woff2") format("woff2"), url("/fonts/roboto.woff") format("woff");
  font-style: normal;
  font-weight: 300; }

html {
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  font-family: "Roboto", "Arial", sans-serif;
  color: #000;
  font-size: 18px;
  line-height: 26px;
  margin: 0;
  padding: 0; }

figure {
  margin: 0; }

img {
  max-width: 100%;
  height: auto; }

.page-header {
  position: absolute;
  background-color: #fff;
  width: 100%;
  min-height: 60px;
  padding-right: 0px;
  padding-left: 0px;
  z-index: 2; }
  @media (min-width: 660px) {
    .page-header {
      min-height: 110px; } }
  @media (min-width: 1534px) {
    .page-header {
      position: absolute; } }

.page-header__logo {
  height: 30px;
  display: inline-block;
  vertical-align: top;
  margin-top: 35px;
  margin-bottom: 15px;
  margin-left: 20px; }

.page-header__wrapper {
  width: 100%; }
  @media (min-width: 1534px) {
    .page-header__wrapper {
      width: 1534px;
      margin-left: auto;
      margin-right: auto;
      display: flex; } }

.opening {
  min-height: 120px; }
  @media (min-width: 1534px) {
    .opening {
      padding-top: 110px;
      display: flex;
      flex-direction: row;
      justify-content: space-between; } }

.opening__in {
  padding-bottom: 23px; }

.opening__title {
  font-size: 50px;
  line-height: 50px;
  font-weight: 900;
  color: #F1F1F1;
  margin: 0;
  position: absolute;
  top: 80px;
  left: 20px;
  right: 0;
  z-index: -1; }
  @media (min-width: 660px) {
    .opening__title {
      font-size: 191px;
      line-height: 191px;
      top: 120px;
      left: 190px; } }

.opening__name {
  position: relative;
  font-weight: 300;
  font-size: 27px;
  line-height: 34px;
  margin: 0;
  top: 110px;
  left: 20px;
  max-width: 250px;
  background: linear-gradient(to right, #2EC73D, #7C64C2 15%, #B31DB6 45%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent; }
  @media (min-width: 660px) {
    .opening__name {
      font-size: 63px;
      line-height: 79px;
      max-width: 322px;
      left: 200px;
      top: 145px; } }

.opening-nav {
  margin-top: 80px; }
  @media (min-width: 660px) {
    .opening-nav {
      margin-right: 64px;
      display: flex; } }

.opening-nav__menu {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  list-style: none; }

.opening-nav__link {
  display: block;
  margin: 0;
  color: #858A8A;
  text-decoration: none;
  font-size: 22px;
  font-weight: 900;
  line-height: 32px;
  padding: 16px 20px; }
  .opening-nav__link[href]:hover {
    color: #000; }
  .opening-nav__link[href]:active {
    color: #000; }

.opening__links {
  display: flex;
  flex-direction: column;
  font-size: 22px;
  margin: 0;
  padding: 0;
  list-style: none; }
  @media (max-width: 1533px) {
    .opening__links {
      box-shadow: 0 0 10px rgba(0, 0, 0, 0.11);
      margin-right: 64px; } }

.wrapper {
  display: flex;
  flex-direction: column; }

.pink {
  color: #940B97; }

.items {
  padding-top: 200px; }

@media (max-width: 660px) {
  .main-nav {
    display: none; } }

.main-nav__menu {
  display: flex;
  flex-direction: column;
  font-size: 22px;
  margin: 0;
  padding: 0;
  list-style: none;
  margin-right: 20px;
  margin-left: 20px; }

.main-nav__link {
  display: block;
  margin: 0;
  color: #858A8A;
  text-decoration: none;
  font-size: 18px;
  font-weight: 700;
  line-height: 32px;
  padding: 16px 20px; }
  .main-nav__link[href]:hover {
    color: #940B97; }

.main-nav__toggle {
  position: absolute;
  top: 21px;
  right: 19px;
  width: 50px;
  height: 24px;
  padding: 0;
  border: none;
  cursor: pointer;
  outline: none; }
  @media (min-width: 660px) {
    .main-nav__toggle {
      display: none; } }

@media (min-width: 1534px) {
  .main-nav {
    margin-left: auto;
    padding-top: 29px;
    flex-shrink: 1; }
  .main-nav__menu {
    flex-wrap: wrap;
    flex-direction: row;
    border-top: none;
    justify-content: flex-end; }
  .main-nav__item {
    margin-left: 79px; }
    .main-nav__item:first-child {
      margin-left: 0; }
    .main-nav__item--active {
      position: relative; }
      .main-nav__item--active::after {
        content: "";
        position: absolute;
        right: 0;
        bottom: -2px;
        left: 0;
        height: 2px;
        background: #fff; }
  .main-nav__link {
    line-height: 18px;
    font-weight: 400;
    padding: 11px 0; } }

.description {
  margin-right: 20px;
  margin-left: 20px; }
  @media (min-width: 1534px) {
    .description {
      position: relative;
      top: 400px;
      left: 400px;
      display: flex;
      flex-direction: row;
      margin-right: 0;
      margin-left: 60px; } }

.description__text {
  font-size: 18px;
  line-height: 26px;
  font-weight: 400;
  text-align: start; }
  @media (min-width: 1534px) {
    .description__text {
      width: 702px;
      padding-bottom: 29px; } }

.pictures {
  padding-top: 48px;
  padding-bottom: 25px;
  margin: 0;
  padding: 0;
  list-style: none; }
  @media (min-width: 660px) {
    .pictures {
      margin-left: 74px; } }

.pictures__list {
  margin: 0;
  padding: 0;
  list-style: none; }

.pictures__list .pictures__item {
  display: none;
  height: 100%;
  width: 100%;
  margin: 0px auto;
  background: white;
  font-size: 40px;
  text-align: center;
  align-items: center; }

.pictures__list .flip__item‐‐active {
  display: block; }

.prev {
  display: none; }

.next {
  display: none; }

.flip {
  max-width: 280px;
  margin-top: 5px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 10px;
  font-size: 0;
  text-align: center;
  background-color: inherit;
  position: relative; }

.flip__item {
  position: relative;
  display: inline-block;
  vertical-align: top;
  width: 24px;
  height: 24px;
  cursor: pointer;
  margin-left: 1px;
  margin-right: 1px; }
  .flip__item::before {
    content: "";
    position: absolute;
    display: block;
    width: 12px;
    height: 12px;
    top: 50%;
    left: 50%;
    border: 1px solid #C4C4C4;
    background-color: transparent;
    transform: translate(-50%, -50%);
    border-radius: 50%; }
  .flip__item:hover::before {
    border: 1px solid #B31DB6; }
  .flip__item:active::before {
    background-color: #C4C4C4;
    transform: translate(-50%, -50%) scale(1, 1); }
  .flip__item‐‐active {
    cursor: default; }
  .flip__item‐‐active::before {
    content: "";
    position: absolute;
    display: block;
    width: 12px;
    height: 12px;
    top: 50%;
    left: 50%;
    border: 1px solid #C4C4C4;
    background-color: transparent;
    transform: translate(-50%, -50%);
    border-radius: 50%; }
  .flip__item‐‐active:hover::before {
    background-color: transparent; }

.main-content {
  display: flex;
  flex-direction: column; }
  @media (min-width: 660px) {
    .main-content {
      flex-direction: row; } }

@media (min-width: 320px) and (max-width: 1533px) {
  .hidden {
    display: none; } }

.container {
  display: none; }

@media (max-width: 1533px) {
  .active {
    border-left: none;
    border-right: none;
    border-top: none;
    border-bottom: 5px solid;
    border-image: linear-gradient(to right, #2EC73D, #7C64C2 15%, #B31DB6 45%); } }

@media (min-width: 1533px) {
  .active {
    border-left: none;
    border-top: none;
    border-bottom: none;
    border-right: 5px solid;
    border-image: linear-gradient(to right 158.11deg, rgba(46, 199, 61, 0.5), rgba(124, 100, 194, 0.5) 15%, rgba(179, 29, 182, 0.5) 95.01%); } }
